import { Button, Container, Grid, Paper, Typography, useMediaQuery } from "@mui/material";
import React, { useState } from "react";
import './About.scss';
import teamLogo from "../../assets/images/knapp_logo.png"


export default function About(){
    const [showMessage, setShowMessage] = useState(false); // Состояние для отображения сообщения

  const handleClick = () => {
    setShowMessage(!showMessage); // Показываем сообщение при клике
  };
  const isMobile = useMediaQuery('(max-width:1150px)');
    return(
        <Container>
            <Grid>
                <div className="title_about">
                    <Typography variant="h4">Про нас</Typography>
                    <img src={`./${teamLogo}`} className="image_1"/>
                    <Button  
                    className="button_join"
                    type="button" 
                    variant="containet"
                    onClick={isMobile? handleClick : null}
                    >
                        <Typography color="secondary">Приєднуйся до команди!</Typography>
                    </Button>
                    {showMessage && (
                        <div className="message_container">
                        <Typography variant="h6">Скоро ви зможете приєднуватись до нашої команди та творити історію разом!</Typography>
                        </div>
                    )}
                </div>
                <div>
                    <Typography variant="h6" className="about_text">Ми команда енергійних учнів, котрі роблять життя інших у нашій школі веселішим, яскравішим та різноманітнішим. Наша діяльність в основному пов'язана з організацією та проведенням різних турнірів та конкурсів, але також ми залучаємось до матеріального покращення нашої школи. На нашому сайті ви також можете написати петиції та підписувати їх! Вчимося демократії! Разом ми непереможна команда, що прагне зробити нашу школу кращою кожен день! Невтомна робота та безмежний ентузіазм наших членів наповнюють наші заходи життям та радістю.</Typography>
                </div>
            </Grid>
            
        </Container>
    )
}